:root {
  --color: #b5c4d1e3;
  --background-color: hsl(323 21% 16%);
  --iconColor:#768ca4;
}
.icon {
  color: #768ca4;
  color: var(--iconColor);
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  padding: 0.25em 1em;
  position: relative;
}

.icon::after{
  pointer-events: none;
  content: "";
  position: absolute;
  background: #5d739d;
  top: 80%;
  left: 0;
  width: 100%;
  height: 90%;
  -webkit-transform: perspective(1em) rotateX(40deg) scale(1, 0.35);
          transform: perspective(1em) rotateX(40deg) scale(1, 0.35);
  -webkit-filter: blur(1em);
          filter: blur(1em);
  opacity: 0;
  transition: opacity 100ms linear;
}
.icon:hover::after{
  opacity: 1;
}
.icon:hover {
  color:#5d739d;
  -webkit-filter:drop-shadow( 0 .1em 0 #5d739d);
          filter:drop-shadow( 0 .1em 0 #5d739d);
}
.projectButton {
  font-size: 1em;
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  color: #b5c4d1e3;
  color: var(--color);
  border: #b5c4d1e3 0.125em solid;
  border: var(--color) 0.125em solid;
  padding: 0.25em 1em;
  border-radius: 0.25em;

  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;

  box-shadow: inset 0 0 0.5em 0 #b5c4d1e3, 0 0 0.5em 0 #b5c4d1e3;

  box-shadow: inset 0 0 0.5em 0 var(--color), 0 0 0.5em 0 var(--color);

  position: relative;
}
.projectButton::before {
  pointer-events: none;
  content: "";
  position: absolute;
  background: #b5c4d1e3;
  background: var(--color);
  top: 120%;
  left: 0;
  width: 100%;
  height: 100%;

  -webkit-transform: perspective(1em) rotateX(40deg) scale(1, 0.35);

          transform: perspective(1em) rotateX(40deg) scale(1, 0.35);
  -webkit-filter: blur(1em);
          filter: blur(1em);
  opacity: 0.7;
}
.projectButton::after{
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 1.5em 0.5em #b5c4d1e3;
  box-shadow: 0 0 1.5em 0.5em var(--color);
  opacity: 0;
  background-color: #b5c4d1e3;
  background-color: var(--color);
  z-index: -1;
  transition: opacity 100ms linear;
    background-color: #b5c4d1e3;
    background-color: var(--color);
}
.projectButton:hover::before,
.projectButton:focus::before{
opacity: 1;

}
.projectButton:hover::after,
.projectButton:focus::after{
  opacity: 1;
}
.projectButton:hover,
.projectButton:focus {
  text-decoration: none;
  color: hsl(323 21% 16%);
  color: var(--background-color);
  text-shadow: none;
}

.titleColor {
