.cardCustomize {
  margin: 1rem;
  border-radius: 16px;
  /* border: 3px solid; */
  /* border-color: #5d98b8; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-row-gap: 0.5rem;
  row-gap: 0.5rem;
  -webkit-align-content: space-between;
          align-content: space-between;
  color: #9a9da0;
  background-color: #27293d;
  box-shadow: 0 -1rem 3rem #1d1e2e;
  transition: 0.2s;
}
.cardCustomize:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 -1rem 3rem #5d98b8;
}

.searchBar {
  float: right;
  display: -webkit-flex;
  display: flex;
}

.footerIcon {
  display: inline-block;
  padding: 0px 12px;
}
.footerStyle {
  color: #9a9da0;
  text-align: center;
}
.restParts {
  color: #e4f0fbe3;
}

